import React from 'react'
import './Wellness.scss'
import { withRouter } from 'react-router-dom'
import { urls } from '../../../routes/routes'
import { connect } from 'react-redux'
import { Clevertap, IsRedBus } from '../../../utils/Utils'
import { LazyImage } from '../../../utils/LazyImage'

function Wellness(props) {

    const HandleGetSTarted=()=>{
        Clevertap('GetStarted',{clicked:true})
        if(props.auth.authStatus===true){
            props.history.push(urls.GSTIN_DETAILS)
        }else{
            props.history.push(urls.VERIFY_PHONE)
        }
    }
 
    return (
        <div className="divBackWellness">
            <div className="wellness">
                <div className="wellnessdiv">
                    <div className="planprice">
                        <div className="innerdiv wel_fnt_chg_k">bits-pilani Plus Membership</div>
                    </div>
                    <div className="rupees-well-cls">
                        <div>
                        <LazyImage src="https://res.cloudinary.com/bits-pilani/image/upload/fl_lossy,f_auto,q_auto/v1594103029/New-home-screen/Vector_5_rc7afm.png"  alt="vector" className="well-line-webview" height="1px" width="100%" />
                        <LazyImage src="https://res.cloudinary.com/bits-pilani/image/upload/fl_lossy,f_auto,q_auto/v1594271658/New-home-screen/Vector_5_2_yoorex.png" alt="vector" className="well-line-mobview" height="1px" width="100%"/>

                        <div className="well_amtCls" style={{marginTop: '0px'}}>â‚¹49</div>
                        <div className="permonth well_dutnCls">PER MEMBER</div>
                    </div>
                    </div>
                </div>
                <div className="getstarted">
                    <div className="checkcontent">
                       <div className="tick-mem-cls">
                           <LazyImage height="" src="https://res.cloudinary.com/bits-pilani/image/upload/fl_lossy,f_auto,q_auto/v1595593575/New-home-screen/Group_449_y5z6vy.svg" alt="checkmark" className="tick-img" />
                        </div>
                        <div className="content contentTxt">
                        Our healthcare membership costs 40% less than other market health packages
                        </div>
                    </div>
                    <div className="checkcontent">
                        <div className="tick-mem-cls">
                            <LazyImage src="https://res.cloudinary.com/bits-pilani/image/upload/fl_lossy,f_auto,q_auto/v1595593575/New-home-screen/Group_449_y5z6vy.svg" alt="checkmark" className="tick-img" />
                        </div>
                        <div className="content contentTxt">
                        Our pricing is completely transparent, with no additional paperwork or charges
                        </div>
                    </div>
                    <div className="checkcontent">
                    <div className="tick-mem-cls">
                        <LazyImage src="https://res.cloudinary.com/bits-pilani/image/upload/fl_lossy,f_auto,q_auto/v1595593575/New-home-screen/Group_449_y5z6vy.svg" alt="checkmark" className="tick-img" />
                        </div>
                        <div className="content contentTxt">
                        Quick and easy, monthly payments to ensure value-for-money
                        </div>
                    </div>
                    <div align="center">
                        <button className="getbtn" onClick={()=>HandleGetSTarted()}>{IsRedBus()}</button>
                    </div>

 
            </div>

            <div className="mobview">
                        <button className="getbtn" onClick={()=>HandleGetSTarted()}>{IsRedBus()}</button>
                    </div>
        </div>

        </div>
    )
}

const mapStateToProps = (state) => ({
    auth:state.navigationStack.authObj
  })

export default withRouter(connect(mapStateToProps, null)(Wellness))
